@import '../../styles/utils/vars';
@import '../../styles/utils/mixins';

* {
  transition-duration: $transition-duration;
  margin: 0;
  padding: 0;
}

.page {
  min-width: 320px;
  scroll-behavior: smooth;
}

.app {
  &__body {
    min-height: calc(100vh - $footer-height-mobile);

    @include on-tablet {
      min-height: calc(100vh - $footer-height-desktop);
    }

    &--menu-active {
      display: none;
    }
  }
}
